import React from "react";
const { Route, Routes } = require("react-router-dom");

class PageRoutes extends React.PureComponent {
    done = false;
    list = [];
    render() {
        if (!this.done) {
            /** @type {String[]} */
            let list = this.list;
            let paths = this.props.paths;
            let element = this.props.element;
            let _else = this.props.else;
            paths.forEach(path => {
                list.push(<Route key={path} path={path} element={element} />)
            });
            if (typeof _else !== "undefined")
                list.push(<Route key="else" path="*" element={_else} />);
            this.done = true;
        }
        return <Routes>{this.list}</Routes>;
    }
}

export default PageRoutes;