import React from "react";
import { Navigate } from "react-router-dom";
import { Pages } from "../StaticData";
import client from "./CLASSES/client";
import PageRoutes from "./PageRoute";

class RequireAuthorization extends React.PureComponent {
    render() {
        if (!client.authorized)
            return <Navigate to="/Home" />;
        return this.props.children;
    }
}

class Authorized extends React.PureComponent {
    render() {
        if (!client.ready)
            return <></>;
        return <PageRoutes paths={Pages.requireAuthorization} element={<RequireAuthorization>{this.props.children}</RequireAuthorization>} else={<></>} />;
    }
}

class NotAuthorized extends React.PureComponent {
    render() {
        return <PageRoutes paths={Pages.NoAuthorization} element={this.props.children} else={<></>} />;
    }
}

export { Authorized, NotAuthorized, RequireAuthorization };